.aboutContainter{display:flex;justify-content:space-evenly;position:relative;background-color:white;flex-wrap:wrap;margin-left:auto;margin-right:auto;padding-top:8vw;background-image:url(/static/media/code-pattrn-bg.1f421243.jpg);background-attachment:fixed;background-repeat:repeat;background-size:250x 250px}.aboutContainter:before{content:"";position:absolute;top:0;left:0;width:100%;height:100%;background:linear-gradient(180deg, #fff, #fff2b9, #fff);opacity:0.95}.card{width:65vw;min-width:250px;max-width:500px;text-align:center;padding:20px;margin-top:auto;margin-bottom:20%;border:1px solid lightgray;box-shadow:0px 0px 5px gray;border-radius:2vw}.card img{width:80%;max-width:250px;border-radius:50%;box-shadow:2px 2px 5px black;margin-bottom:15px}.card .card-body{width:85%;margin-left:auto;margin-right:auto}.card .card-body .card-text{font-size:15px}.card .card-body svg{transform:scale(1.75);-webkit-filter:contrast(20%);filter:contrast(20%)}.card .card-body svg:hover{transform:scale(1.75);-webkit-filter:contrast(100%);filter:contrast(100%)}.card p svg{position:absolute;bottom:50px;right:50px;fill:gray;transform:scale(2.5);-webkit-animation-name:ckw;animation-name:ckw;-webkit-animation-duration:3s;animation-duration:3s;-webkit-animation-iteration-count:infinite;animation-iteration-count:infinite}@-webkit-keyframes ckw{0%{transform:scale(2) rotate(360deg)}100%{transform:scale(2) rotate(0deg)}}@keyframes ckw{0%{transform:scale(2) rotate(360deg)}100%{transform:scale(2) rotate(0deg)}}.card p svg:hover{-webkit-animation-name:ckx;animation-name:ckx;-webkit-animation-duration:0s;animation-duration:0s;-webkit-animation-iteration-count:infinite;animation-iteration-count:infinite}@-webkit-keyframes ckx{0%{transform:scale(2) rotate(0deg)}100%{transform:scale(2) rotate(0deg)}}@keyframes ckx{0%{transform:scale(2) rotate(0deg)}100%{transform:scale(2) rotate(0deg)}}.progress-programming{position:relative;align-self:center;max-width:500px;width:65vw;padding:70px 50px 100px 50px;margin-top:auto;margin-bottom:20%;border:1px solid lightgray;box-shadow:0px 0px 5px gray;border-radius:2vw;background-color:white}.progress-programming h3{font-size:1.25rem;padding:5px 0px;text-align:center}.progress-programming h4{font-size:1rem;padding-top:8px;color:gray}.progress-programming .progress{width:100%;height:15px;background-color:#dbdbdb}.progress-programming p svg{position:absolute;bottom:50px;right:50px;fill:gray;transform:scale(2.5);-webkit-animation-name:ckw;animation-name:ckw;-webkit-animation-duration:3s;animation-duration:3s;-webkit-animation-iteration-count:infinite;animation-iteration-count:infinite}@keyframes ckw{0%{transform:scale(2) rotate(0deg)}100%{transform:scale(2) rotate(360deg)}}.progress-programming p svg:hover{-webkit-animation-name:ckx;animation-name:ckx;-webkit-animation-duration:0s;animation-duration:0s;-webkit-animation-iteration-count:infinite;animation-iteration-count:infinite}@keyframes ckx{0%{transform:scale(2) rotate(0deg)}100%{transform:scale(2) rotate(0deg)}}@media (min-width: 2400px){.card{border-radius:2vw}}

