kx;animation-name:ckx;-webkit-animation-duration:0s;animation-duration:0s;-webkit-animation-iteration-count:infinite;animation-iteration-count:infinite}@-webkit-keyframes ckx{0%{transform:scale(2) rotate(0deg)}100%{transform:scale(2) rotate(0deg)}}@keyframes ckx{0%{transform:scale(2) rotate(0deg)}100%{transform:scale(2) rotate(0deg)}}

